@import "https://fonts.googleapis.com/css?family=Montserrat:500";
@import "https://fonts.googleapis.com/css?family=PT+Sans|Roboto+Slab";
body.dark {
  background: #141414;
  color: #e6e6e6; }
  body.dark .section-links a {
    color: #e6e6e6 !important; }
  body.dark img {
    opacity: .8; }
    body.dark img:hover {
      opacity: 1; }
  body.dark .content .section-large[data-background='bionic'] {
    background-color: #071c07; }

.flex {
  display: flex;
  flex-wrap: wrap; }
  @media screen and (max-width: 75rem) {
    .flex {
      justify-content: center; } }

.section,
.section-content,
.footer,
p,
h1,
h2 {
  clear: both;
  padding: .1em .8em; }

h1 {
  font-size: 1.3rem; }

h2 {
  font-size: 1.2rem;
  padding-left: 2em; }

h3, h4, h5, h6 {
  font-size: 1.1rem; }

h1, h2, h3, h4, h5, h6 {
  font-family: 'PT Sans',sans-serif; }

p {
  font-family: 'Roboto Slab',serif;
  font-size: 1rem;
  max-width: 700px; }

a, a:visited {
  color: #4baddd;
  text-decoration: none; }
  a:hover, a:visited:hover {
    text-decoration: underline; }

body {
  background: -moz-linear-gradient(left, #e8e5d7 0%, #fffced 42%, #fffced 100%);
  background: -webkit-linear-gradient(left, #e8e5d7 0%, #fffced 42%, #fffced 100%);
  background: linear-gradient(to right, #e8e5d7 0%, #fffced 42%, #fffced 100%);
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
  margin: 0;
  padding: 0;
  font-size: 15px; }

br {
  margin-bottom: 1rem; }

code {
  font-family: source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace; }

.menu-item:hover > .sub-menu-indicator svg[data-icon='box'],
.menu-item.active > .sub-menu-indicator svg[data-icon='box'],
.menu-item.sub-active > .sub-menu-indicator svg[data-icon='box'] {
  opacity: 0; }

.menu-item:hover > .sub-menu-indicator svg[data-icon='box-open'],
.menu-item.active > .sub-menu-indicator svg[data-icon='box-open'],
.menu-item.sub-active > .sub-menu-indicator svg[data-icon='box-open'] {
  opacity: 1; }

.menu-item:hover > .sub-menu,
.active > .sub-menu,
.sub-active > .sub-menu {
  max-height: 20rem !important; }

.sub-active, .sub-menu {
  background: #e6e6e6;
  color: #1e1e1e !important; }

.active {
  background: #d1d1d1;
  color: #141414 !important; }

.icon {
  color: #e6e6e6;
  cursor: pointer;
  font-size: 1.3rem;
  width: 10rem;
  height: 1.5rem; }
  .icon:hover div {
    transform: scale(1, 1); }
  .icon:hover .one {
    margin: .5rem 0 0 .85rem; }
  .icon:hover .two {
    margin: 0 0 0 3.5rem; }
  .icon:hover .three {
    font-size: .8rem;
    margin: .1rem 0 0 4rem; }
  .icon div {
    font-style: normal;
    position: absolute;
    transition: 0.26s ease-in-out; }
  .icon .one {
    font-size: .8rem;
    margin: 0 0 0 2rem; }
  .icon .two {
    color: #4baddd;
    margin: .2rem 0 0 6.5rem; }
  .icon .three {
    color: #4baddd;
    font-size: .7rem;
    margin: .6rem 0 0 2rem; }
  .icon .h {
    transform: scale(-1, 1); }
  .icon .vh {
    transform: scale(-1, -1); }

nav {
  font-family: 'Montserrat',sans-serif;
  background: -moz-linear-gradient(left, #162618 0%, #162618 71%, #0e2310 99%);
  background: -webkit-linear-gradient(left, #162618 0%, #162618 71%, #0e2310 99%);
  background: linear-gradient(to right, #162618 0%, #162618 71%, #0e2310 99%);
  background: #162618;
  color: #e6e6e6;
  height: 100vh;
  left: 0;
  top: 0;
  overflow: hidden;
  position: fixed;
  transition: width 0.26s ease-in-out;
  width: 190px; }
  nav.small {
    width: 2.5rem; }
    nav.small > .menu-item .sub-menu-indicator {
      opacity: 0; }
    nav.small ~ .content {
      padding-left: 2.5rem;
      width: calc(100% - 2.5rem); }
  nav.hidden {
    width: 0; }
  nav .menu-item {
    cursor: pointer;
    display: flex;
    font-family: 'Montserrat',sans-serif;
    overflow: hidden;
    padding: 0 .1em;
    width: 190px; }
    nav .menu-item[data-filler='true'] {
      height: .6rem; }
    nav .menu-item .menu-item-wrapper {
      margin: 0;
      padding: 0;
      position: relative;
      width: 100%; }
      nav .menu-item .menu-item-wrapper span {
        padding: .3em .7em;
        position: absolute; }
        nav .menu-item .menu-item-wrapper span:hover {
          text-decoration: underline; }
      nav .menu-item .menu-item-wrapper i[data-state='off'] > svg {
        transform: rotate(90deg); }
      nav .menu-item .menu-item-wrapper svg {
        transition: 0.26s ease-in-out;
        overflow: hidden;
        padding: 6px;
        width: 20px !important; }
    nav .menu-item .sub-menu-indicator {
      position: absolute;
      right: .5em;
      padding: .5em;
      transition: 0.26s ease-in-out; }
      nav .menu-item .sub-menu-indicator svg {
        position: absolute;
        right: 0;
        transition: 0.26s ease-in-out; }
      nav .menu-item .sub-menu-indicator svg[data-icon='box-open'] {
        right: -.15em;
        opacity: 0; }
    nav .menu-item .sub-menu {
      margin: 2rem 0 0 -190px;
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.26s ease-in-out; }
      nav .menu-item .sub-menu .menu-item {
        font-size: .8rem; }

.content {
  font-family: 'Crete Round',serif;
  line-height: 1.5em;
  min-height: 100vh;
  overflow: hidden;
  padding-left: 190px;
  transition: 0.26s ease-in-out;
  width: calc(100% - 190px);
  margin: 0 auto;
  max-width: 70rem; }
  .content .section {
    clear: both;
    padding: .3rem;
    margin: 1rem;
    border-bottom: 1px solid;
    border-color: #646464;
    border-color: rgba(150, 150, 150, 0.5);
    -webkit-background-clip: padding-box;
    /* for Safari */
    background-clip: padding-box;
    /* for IE9+, Firefox 4+, Opera, Chrome */ }
    @media screen and (max-width: 75rem) {
      .content .section {
        margin: .2rem; } }
    .content .section[data-background='star'] {
      animation: animatedBackground_bg 130s linear infinite;
      background-color: #141414;
      background-image: url(./resources/stars_bg.gif);
      color: #e6e6e6 !important; }
      .content .section[data-background='star'] .section-image-wrapper {
        animation: animatedBackground 90s linear infinite;
        background-image: url(./resources/stars.gif); }
      .content .section[data-background='star'] .section-links a {
        color: #e6e6e6 !important; }
      .content .section[data-background='star'] p, .content .section[data-background='star'] h1, .content .section[data-background='star'] img {
        color: #e6e6e6; }
    .content .section[data-background='bionic'] {
      background-color: #88db43; }
    .content .section.section-large {
      background-repeat: repeat;
      min-height: 25rem;
      margin: 0;
      padding: 1.3em;
      width: calc(100% - 2.6em); }
      @media screen and (max-width: 75rem) {
        .content .section.section-large {
          padding: 1rem; } }
    .content .section .section-image-wrapper {
      background-repeat: repeat;
      min-height: 25rem;
      width: 100%; }
      .content .section .section-image-wrapper p, .content .section .section-image-wrapper h1,
      .content .section .section-image-wrapper .section-left-header h1,
      .content .section .section-image-wrapper .section-right img {
        margin: .6rem;
        padding: .5rem .8rem; }
      .content .section .section-image-wrapper .section-lr {
        padding: 1rem; }
        .content .section .section-image-wrapper .section-lr .section-left, .content .section .section-image-wrapper .section-lr .section-right {
          min-width: 25rem;
          width: 50%; }
          @media screen and (max-width: 50rem) {
            .content .section .section-image-wrapper .section-lr .section-left, .content .section .section-image-wrapper .section-lr .section-right {
              min-width: 0;
              width: 100%; } }
        .content .section .section-image-wrapper .section-lr .section-left .section-left-header {
          width: 100%; }
        .content .section .section-image-wrapper .section-lr .section-left .section-left-header h1 {
          float: left; }
        .content .section .section-image-wrapper .section-lr .section-left .section-left-header .section-links {
          float: left;
          font-size: 1.6rem;
          margin-top: .8rem; }
          .content .section .section-image-wrapper .section-lr .section-left .section-left-header .section-links a {
            color: #232323;
            margin: 1em; }
        .content .section .section-image-wrapper .section-lr .section-right img {
          max-width: calc(100% - 1.2em);
          max-height: 200px !important; }

.footer {
  padding: 2rem;
  width: calc(100% - 4rem);
  justify-content: center; }
  .footer p {
    font-size: .8rem;
    text-align: center; }

@keyframes animatedBackground {
  from {
    background-position: 0 0; }
  to {
    background-position: 640px 400px; } }

@-webkit-keyframes animatedBackground {
  from {
    background-position: 0 0; }
  to {
    background-position: 640px 400px; } }

@keyframes animatedBackground_bg {
  from {
    background-position: 0 0; }
  to {
    background-position: -640px -800px; } }

@-webkit-keyframes animatedBackground_bg {
  from {
    background-position: 0 0; }
  to {
    background-position: -640px 400px; } }
